import React, { useEffect } from 'react';
import { io } from "socket.io-client"; // Import Socket.IO client
import { Canvas } from '@react-three/fiber';
import { Physics } from '@react-three/cannon';
import { Sky } from '@react-three/drei';
import { Ground } from './components/Ground';
import { Player } from './components/Player';
import { FPV } from './components/FPV';
import { Cubes } from './components/Cubes';
import { TextureSelector } from './components/TextureSelector';
import { Menu } from './components/Menu';
import { useStore } from './hooks/useStore';

function App() {
    const loadWorld = useStore(state => state.loadWorld); // Get loadWorld function from the store
    const socket = io("http://localhost:3000"); // Initialize Socket.IO client

    useEffect(() => {
        if (loadWorld) {
            loadWorld(); // Load cubes from local storage when the app starts
        } else {
            console.error("loadWorld is undefined");
        }

        // Socket.IO event listeners
        socket.on("connect", () => {
            console.log("Connected to server");
        });

        socket.on("message", (data) => {
            console.log("Message from server:", data);
        });

        socket.on("playerMoved", (data) => {
            console.log("Player moved:", data);
            // Update your player state here based on data received
        });

        return () => {
            socket.disconnect(); // Cleanup on unmount
        };
    }, [loadWorld, socket]);

    // Function to emit player move
    const handlePlayerMove = (direction) => {
        socket.emit("playerMove", { direction });
    };

    // Keydown event handler
    const handleKeyDown = (event) => {
        if (event.key === "ArrowUp") {
            handlePlayerMove("up");
        } else if (event.key === "ArrowDown") {
            handlePlayerMove("down");
        }
        // Add more controls as needed
    };

    useEffect(() => {
        window.addEventListener("keydown", handleKeyDown);

        return () => {
            window.removeEventListener("keydown", handleKeyDown);
        };
    }, []);

    return (
        <>
            <Canvas shadows>
                <directionalLight
                    position={[50, 50, 50]}
                    intensity={1.5}
                    castShadow
                    shadow-camera-left={-40}
                    shadow-camera-right={40}
                    shadow-camera-top={40}
                    shadow-camera-bottom={-40}
                    shadow-camera-near={0.1}
                    shadow-camera-far={200}
                    shadow-bias={-0.0001}
                    shadow-mapSize={[2048, 2048]}
                />
                <ambientLight intensity={0.2} />
                <FPV />
                <Physics>
                    <Player />
                    <Cubes />
                    <Ground />
                </Physics>
            </Canvas>
            <div className="absolute centered cursor">+</div>
            <TextureSelector />
            <Menu />
        </>
    );
}

export default App;
